.loading_loader__1ViEP span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    animation: loading_animate__1AXDW 3s ease-in-out infinite;
}

.loading_loader__1ViEP span:before {
    content: "";
    position: absolute;
    top: 0;
    left: calc(50% - 20px);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-shadow: 0 0 30px #03a9f4;
    background: linear-gradient(#fce4ec, #03a9f4);
}

.loading_loader__1ViEP span:nth-child(1) {
    animation-delay: 0.2s;
}

.loading_loader__1ViEP span:nth-child(2) {
    animation-delay: 0.4s;
}

.loading_loader__1ViEP span:nth-child(3) {
    animation-delay: 0.6s;
}

.loading_loader__1ViEP span:nth-child(4) {
    animation-delay: 0.8s;
}

.loading_loader__1ViEP span:nth-child(5) {
    animation-delay: 1s;
}

.loading_loader__1ViEP span:nth-child(6) {
    animation-delay: 1.2s;
}

.loading_loader__1ViEP span:nth-child(7) {
    animation-delay: 1.4s;
}

@keyframes loading_animate__1AXDW {
    0% {
        transform: rotate(0);
    }
    50%,
    100% {
        transform: rotate(360deg);
    }
}


